events.on('ready', function() {
    $('.botao').on('click', function(){
        $(this).removeClass('infinite');
    })

    $('video').attr('tabindex', '-1')

    $('.owl-next').addClass('animated pulse infinite');

    $('.line-interation').find('.line-major').addClass('animated fadeInRight');
    $('.line-interation').find('.botao').on('click', function(){
        $(this).parent().parent('.line-minor').siblings('.line-major').css('display','block');
    })

    // Obter a altura do cabeçalho dinamicamente
    var ajuste = $('.component-template-header').outerHeight() || 0;

    // Armazenar os pontos dinamicamente
    var pontos = [];
    $('[id^="ponto"]').each(function () {
        pontos.push($(this).offset().top - ajuste);
    });

    // console.log('Alturas calculadas:', pontos);

    // Função para atualizar os pontos em caso de resize
    function atualizarPontos() {
        ajuste = $('.component-template-header').outerHeight() || 0;
        pontos = [];
        $('[id^="ponto"]').each(function () {
            pontos.push($(this).offset().top - ajuste);
        });
    }

    // Escutar o evento de scroll
    $(window).on('scroll', function () {
        var scrollAtual = $(this).scrollTop(); // Posição atual do scroll

        // Iterar pelos pontos para verificar o intervalo
        for (var i = 0; i < pontos.length; i++) {
            if (scrollAtual >= pontos[i] && (i === pontos.length - 1 || scrollAtual < pontos[i + 1])) {
                $('.opMenu').removeClass('active');
                $('.op0' + (i + 1)).addClass('active');
                // console.log(`Topo ${i + 1}`);
                break;
            }
        }
    });

    // Atualizar valores ao redimensionar a janela
    $(window).on('resize', atualizarPontos);

});